import React, { useState, useEffect } from 'react';
import './BlogSingle.css';
import { useParams } from 'react-router-dom';


function BlogSingle() {

    const params = useParams();
    const { slug } = params;

    const [Users, fetchUsers] = useState([]);
    const [isLoading, setIsLoading] = useState(false);

    useEffect(() => {
        const getData = () => {
            setIsLoading(true);
            fetch(`https://dreamhousemakerz.com/production/index.php/wp-json/wp/v2/posts?slug=${slug}`)
                .then((res) => res.json())
                .then((res) => {
                    //console.log(res)
                    fetchUsers(res)
                    setIsLoading(false);
                })
        }
        getData();
    }, []);

    return (
        <>
            <section className='blog-banner-main'>
                <div className='blog-banner-sec'>
                    <img alt="blog" src="https://dreamhousemakerz.com/production/wp-content/uploads/2022/12/inner-pages-banner.jpg" />
                </div>
            </section>
            {isLoading ? <div className="loader-spinner-p" ><div className="lds-spinner"><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div></div></div> :
                <section className='blog-single-main'>
                    <div className='container-size'>
                        <div className='blog-single-inner'>
                            {Users.map((item, i) => {
                                return <>
                                    <h3 key={i}>{item.title.rendered}</h3>
                                    <div className='blog-single-image'>
                                        <img src={item.featured_url} />
                                    </div>
                                    <div className='blog-single-content' dangerouslySetInnerHTML={{ __html: item.content.rendered }} />
                                </>
                            })}
                        </div>
                    </div>
                </section>
            }
        </>
    );
}
export default BlogSingle;