import "./CompleteCustomized.css";
import React from "react";
import { Link } from "react-router-dom";

class CompleteCustomized extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      items: [],
      isLoaded: false,
    };
  }

  componentDidMount() {
    fetch(
      "https://dreamhousemakerz.com/production/index.php/wp-json/wp/v2/pages/10/"
    )
      .then((result) => result.json())
      .then((json) => {
        this.setState({
          items: json,
          isLoaded: true,
        });
      });
  }

  render() {
    var { isLoaded, items } = this.state;

    if (!isLoaded) {
      return <div className="loader"></div>;
    } else {
      return (
        <>
          <section
            className="complete-customized-main"
            style={{
              backgroundImage: `url('${items.acf.complete_customized.bg_images}')`,
            }}
          >
            <div className="complete-customized-inner">
              <h3>
                <strong> {items.acf.complete_customized.title}</strong>
                <br />
                {items.acf.complete_customized.sub_title}
              </h3>
              <div className="customized-btn">
                <div className="customized-btn-left">
                  <a
                    target="_blank"
                    rel="noreferrer"
                    href="https://calendly.com/dreamhousemakerz/strategy-session?month=2022-08"
                  >
                    {items.acf.complete_customized.button_title_1}
                  </a>
                </div>
                <div className="customized-btn-right">
                  <Link to="/Drawings">
                    {items.acf.complete_customized.button_title_2}
                  </Link>
                </div>
              </div>
            </div>
          </section>

          <section className="archtecture_visual_art">
            <div className="container-size">
              <div className="heading-container-wrap">
                <h2>Architecture is a visual art. </h2>
                <p>
                  We at Dream House Makerz are known for transforming a normal
                  house into an ideal home. We are a set of house designers who
                  work hard to offer prompt, dependable and professional
                  services at a reasonable price.{" "}
                </p>
                <p>
                  Our team of experts are highly creative and cooperative while
                  creating your house design which are environmentally sensitive
                  and vastu appropriate. We also specialize in interior design,
                  floor plan, office interior design, single floor house design,
                  normal house front elevation designs, and many more services.
                </p>
                <p>
                  Dream House Makerz are one of the top home designers with a
                  belief that every house can become a home with some elevation
                  designs. We assist you with all of your design and layout
                  expectations without demanding a time-consuming and costly
                  visit to an engineer or a home designer.{" "}
                </p>
                <p>
                  Plan your dream design for home with us and achieve your
                  “sapno ka ghar” in an effortless manner.{" "}
                </p>
              </div>
            </div>
          </section>
        </>
      );
    }
  }
}

export default CompleteCustomized;
