import React, { useState, useEffect } from 'react';
import BannerImg from "./images/banner-bungalow.jpg";
import { Link } from "react-router-dom";
import square from "./images/square-feet.png";
import east from "./images/east-direction.png";
import squirearea from "./images/squire-area.png";
import {Helmet} from "react-helmet";


export default function Bungalow() {

    if (localStorage.getItem("Bungalow") == null) {
        var user1 = [];
        localStorage.setItem('Bungalow', JSON.stringify(user1));
    }

    const [BungalowData, BungalowDetails] = useState(JSON.parse(localStorage.getItem('Bungalow')));

    useEffect(() => {
        const getSimplexData = () => {
            fetch(`https://dreamhousemakerz.com/production/index.php/wp-json/wp/v2/services?service_category=64&per_page=100`)
                .then(async (res) => res.json())
                .then((res) => {
                    localStorage.setItem('Bungalow', JSON.stringify(res));
                    BungalowDetails(JSON.parse(localStorage.getItem('Bungalow')));
                });
        }

        getSimplexData()
    }, [])

    return (
        <>
        <Helmet>
                    <title>Living Room Interior Design | Living Room Design Plan and Ideas</title>
                    <meta
                    name='description'
                    content='Modern Bungalow Design Plan | Modern House Design'
                    />
                    <meta
                    name='keyword'
                    content='Maths, Science, Biology, Zology' 
                    />
                </Helmet>
            <section className='about-banner-main'>
                <div className='about-banner-sec'>
                    <img alt="elevation banner" src={BannerImg} />
                </div>
            </section>


            <section className='elevations-section'>
                <div className='container-size'>
                    <div className='heading-plan'>
                        <h1>Bungalow Design</h1>
                        <p> 
                             Giving a look that grabs the most eyeballs is what we aim for. Thus, bringing you the visionary 
                             designs we make sure that your bungalow looks nothing less than a palace. At, Dream House Makerz,
                             we ensure you your Dream Palace in the most efficient manner.</p>
                    </div>

                    <div className='row'>
                        <div className='list-category-box'>
                            {BungalowData.map((item, i) => {
                                return <div className='cate-area-box' key={i}>
                                    <div className='cate-area-left'>
                                        <Link to={`/bungalow/${item.slug}`}><img alt={item.acf.code} src={item.featured_url} /></Link>
                                    </div>
                                    <div className='cate-list-right'>
                                        <div className='cate-inner-top'>
                                            <div className='code-area'>
                                                <p>DESIGN CODE</p>
                                                <h4>{item.acf.code}</h4>
                                            </div>
                                            <ul>
                                                <li><img alt='plot_size' src={squirearea} /><span>{item.acf.plot_size}</span></li>
                                                <li><img alt='area' src={square} /><span>{item.acf.area}</span></li>
                                                <li><img alt='plot_facing' src={east} /><span> {item.acf.plot_facing}</span></li>
                                            </ul>
                                            <h3>Simplex House Plan</h3>
                                        </div>
                                        <div className='cate-inner-bottome'>
                                            <Link className="btn" type="button" to={`/bungalow/${item.slug}`}>View Details</Link>
                                        </div>
                                    </div>
                                </div>
                            })}
                        </div>
                    </div>
                </div>
            </section>
        </>

    )
}