import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import './InteriorDesignCategory.css';
import { Link } from 'react-router-dom';
import square from "../plans/plans-banner/images/square-feet.png";
import east from "../plans/plans-banner/images/east-direction.png";
import squirearea from "../plans/plans-banner/images/squire-area.png";
import {Helmet} from "react-helmet";


function InteriorDesignCategory() {
    const params = useParams();
    const { slug } = params;
    //console.warn(slug);
    const title = slug.replaceAll('-', ' ');

    const [Users, fetchUsers] = useState([]);
    const [isLoading, setIsLoading] = useState(false);

    useEffect(() => {
        const getData = () => {
            setIsLoading(true);
            fetch(`https://dreamhousemakerz.com/production/index.php/wp-json/wp/v2/services/?filter[service_category]=${slug}`)
                .then((res) => res.json())
                .then((res) => {
                    //console.log(res)
                    fetchUsers(res)
                    setIsLoading(false);
                })
        }
        getData();
    }, []);

    return (
        <>   {Users.map((item, i) => { return <>
            <Helmet>
                <title>{title}</title>
                <meta
                name='description'
                content='We create interior design for home which is a blend of traditional and contemporary styles specific to your personal needs.' 
                />
            </Helmet>
                  </>
                })}



            {isLoading ? <div className="loader-spinner-p" > <div className="lds-spinner"><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div></div></div> :
                <>
                    <section className='elevations-section demoo'>
                        <div className='container-size'>
                            <div className='row'>
                                <div className='row'>
                                    <div className="heading-plan">
                                        <h1>{title}</h1>
                                        <p>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. </p>
                                    </div>
                                    <div className='list-category-box list-category-box-main'>
                                        {Users.map((item, i) => {
                                            return <div className='cate-area-box' key={i}>
                                                <div className='cate-area-left'>
                                                    <Link to={`/interiors/${item.slug}`}><img alt={item.acf.code} src={item.featured_url} /></Link>
                                                </div>
                                                <div className='cate-list-right'>
                                                    <div className='cate-inner-top'>
                                                        <div className='code-area'>
                                                            <p>DESIGN CODE</p>
                                                            <h4>{item.acf.code}</h4>
                                                        </div>
                                                        <ul>
                                                            <li><img alt="plot_size" src={squirearea} /><span>{item.acf.plot_size}</span></li>
                                                            <li><img alt="area" src={square} /><span>{item.acf.area}</span></li>
                                                            <li><img alt="plot_facing" src={east} /><span> {item.acf.plot_facing}</span></li>
                                                        </ul>
                                                        <h3>Simplex House Plan</h3>
                                                    </div>
                                                    <div className='cate-inner-bottome'>
                                                        <Link className="btn" type="button" to={`/interiors/${item.slug}`}>View Details</Link>
                                                    </div>
                                                </div>
                                            </div>
                                        })}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
                </>
            }
        </>
    );
}
export default InteriorDesignCategory;



