import React, { useState, useEffect } from 'react';
import BannerImg from "./images/banner-farm-house.jpg";
import { Link } from "react-router-dom";
import square from "./images/square-feet.png";
import east from "./images/east-direction.png";
import squirearea from "./images/squire-area.png";

export default function ShopWithHouse() {

    if (localStorage.getItem("ShopWithHouse") == null) {
        var user1 = [];
        localStorage.setItem('ShopWithHouse', JSON.stringify(user1));
    }

    const [ShopWithHouseData, ShopWithHouseDetails] = useState(JSON.parse(localStorage.getItem('ShopWithHouse')));

    useEffect(() => {
        const getSimplexData = () => {
            fetch(`https://dreamhousemakerz.com/production/index.php/wp-json/wp/v2/services?service_category=61&per_page=100`)
                .then(async (res) => res.json())
                .then((res) => {
                    localStorage.setItem('ShopWithHouse', JSON.stringify(res));
                    ShopWithHouseDetails(JSON.parse(localStorage.getItem('ShopWithHouse')));
                });
        }

        getSimplexData()
    }, [])

    return (
        <>
            <section className='about-banner-main'>
                <div className='about-banner-sec'>
                    <img alt="elevation banner" src={BannerImg} />
                </div>
            </section>


            <section className='elevations-section'>
                <div className='container-size'>
                    <div className='heading-plan'>
                        <h1>Shop With House</h1>
                        <p>The contemporary designs with friendly porches and well-designed bedrooms is what covers a vibe for the farmhouses. With garden-kitchens, we offer you your health on nature’s doorstep. Thus, giving your home a glorious look with the varied features of a proper home.</p>
                    </div>

                    <div className='row'>
                        <div className='list-category-box'>
                            {ShopWithHouseData.map((item, i) => {
                                return <div className='cate-area-box' key={i}>
                                    <div className='cate-area-left'>
                                        <Link to={`/shop-with-house/${item.slug}`}><img alt={item.acf.code} src={item.featured_url} /></Link>
                                    </div>
                                    <div className='cate-list-right'>
                                        <div className='cate-inner-top'>
                                            <div className='code-area'>
                                                <p>DESIGN CODE</p>
                                                <h4>{item.acf.code}</h4>
                                            </div>
                                            <ul>
                                                <li><img alt='plot_size' src={squirearea} /><span>{item.acf.plot_size}</span></li>
                                                <li><img alt='area' src={square} /><span>{item.acf.area}</span></li>
                                                <li><img alt='plot_facing' src={east} /><span> {item.acf.plot_facing}</span></li>
                                            </ul>
                                            <h3>Simplex House Plan</h3>
                                        </div>
                                        <div className='cate-inner-bottome'>
                                            <Link className="btn" type="button" to={`/shop-with-house/${item.slug}`}>View Details</Link>
                                        </div>
                                    </div>
                                </div>
                            })}
                        </div>
                    </div>
                </div>
            </section>
        </>

    )
}