import "./GetInTouch.css";
import React from "react";

class GetInTouch extends React.Component {


  constructor(props) {
    super(props);

    this.state = {
      items: [],
      isLoaded: false,
    };

    this.state = {
      emailValue: "",
      Name: "",
      Phone: "",
      State: "",
      City: "",
      Requirement: "",
    };
  }

  componentDidMount() {
    fetch(
      "https://dreamhousemakerz.com/production/index.php/wp-json/wp/v2/pages/10/"
    )
      .then((result) => result.json())
      .then((json) => {
        this.setState({
          items: json,
          isLoaded: true,
        });
      });
  }

  render() {
    var { isLoaded, items } = this.state;

    if (!isLoaded) {
      return <div className="loader"></div>;
    } else {
      return (
        <section className="get-in-touch-main">
          <div className="get-in-touch-img">
            <img alt="Get in touch" src={items.acf.get_in_touch.left_image} />
          </div>
          <div className="get-in-touch-form">
            <h3>Get in touch</h3>
            <p>
              Whether you're ready to sell residential, luxury, or land-we're
              ready to help get it done.
            </p>

            <form
              action="https://Dreamhousemakerz.us21.list-manage.com/subscribe/post?u=64c2b3ae17cec593e663019b1&amp;id=ff2b45275c&amp;f_id=00cfcae1f0" method="post" id="mc-embedded-subscribe-form" name="mc-embedded-subscribe-form" className="validate" target="_self"
            >
              <input type="hidden" name="u" value="eb05e4f830c2a04be30171b01" />
              <input type="hidden" name="id" value="8281a64779" />

              <div className="col-md-6">
                <label htmlFor="MERGE0">
                  <input
                    type="text"
                    name="NAME"
                    id="MERGE0"
                    placeholder="First name"
                    value={this.state.Name}
                    onChange={(e) => {
                      this.setState({ Name: e.target.value });
                    }}
                  />
                </label>
              </div>

              <div className="col-md-6">
                <label htmlFor="MERGE2">
                  <input
                    type="text"
                    name="PHONE"
                    id="MERGE2"
                    placeholder="Your Number"
                    maxLength="10"
                    value={this.state.Phone}
                    onChange={(e) => {
                      this.setState({ Phone: e.target.value });
                    }}
                  />
                </label>
              </div>

              <div className="col-md-12">
                <label htmlFor="MERGE1">
                  <input
                    type="email"
                    name="EMAIL"
                    id="MERGE1"
                    placeholder="Email"
                    value={this.state.emailValue}
                    onChange={(e) => {
                      this.setState({ emailValue: e.target.value });
                    }}
                    autoCapitalize="off"
                    autoCorrect="off"
                  />
                </label>
              </div>

              <div className="col-md-6">
                <label htmlFor="MERGE3">
                  <select
                    name="STATE"
                    value={this.state.State}
                    onChange={(e) => {
                      this.setState({ State: e.target.value });
                    }}
                    id="MERGE3"
                  >
                    <option value="State">State</option>
                    <option value="Andhra Pradesh">Andhra Pradesh</option>
                    <option value="Arunachal Pradesh">Arunachal Pradesh</option>
                    <option value="Assam">Assam</option>
                    <option value="Bihar">Bihar</option>
                    <option value="Chhattisgarh">Chhattisgarh</option>
                    <option value="Goa">Goa</option>
                    <option value="Gujarat">Gujarat</option>
                    <option value="Haryana">Haryana</option>
                    <option value="Himachal Pradesh">Himachal Pradesh</option>
                    <option value="Jammu and Kashmir">Jammu and Kashmir</option>
                    <option value="Jharkhand">Jharkhand</option>
                    <option value="Karnataka">Karnataka</option>
                    <option value="Kerala">Kerala</option>
                    <option value="Madhya Pradesh">Madhya Pradesh</option>
                    <option value="Maharashtra">Maharashtra</option>
                    <option value="Manipur">Manipur</option>
                    <option value="Meghalaya">Meghalaya</option>
                    <option value="Mizoram">Mizoram</option>
                    <option value="Nagaland">Nagaland</option>
                    <option value="Orissa">Orissa</option>
                    <option value="Punjab">Punjab</option>
                    <option value="Rajasthan">Rajasthan</option>
                    <option value="Sikkim">Sikkim</option>
                    <option value="Tamil Nadu">Tamil Nadu</option>
                    <option value="Tripura">Tripura</option>
                    <option value="Uttarakhand">Uttarakhand</option>
                    <option value="Uttar Pradesh">Uttar Pradesh</option>
                    <option value="West Bengal">West Bengal</option>
                    <option value="Tamil Nadu">Tamil Nadu</option>
                    <option value="Andaman and Nicobar Islands">
                      Andaman and Nicobar Islands
                    </option>
                    <option value="Chandigarh">Chandigarh</option>
                    <option value="Dadra and Nagar Haveli">
                      Dadra and Nagar Haveli
                    </option>
                    <option value="Daman and Diu">Daman and Diu</option>
                    <option value="Delhi">Delhi</option>
                    <option value="Lakshadweep">Lakshadweep</option>
                    <option value="Pondicherry">Pondicherry</option>
                  </select>
                </label>
              </div>

              <div className="col-md-6">
                <label htmlFor="MERGE3">
                  <input
                    type="text"
                    name="CITY"
                    id="MERGE4"
                    value={this.state.City}
                    onChange={(e) => {
                      this.setState({ City: e.target.value });
                    }}
                    placeholder="City"
                  />
                </label>
              </div>

              <div className="col-md-12">
                <label htmlFor="MERGE3">
                  <input
                    type="text"
                    name="MMERGE6"
                    id="MERGE5"
                    placeholder="Requirement"
                    value={this.state.Requirement}
                    onChange={(e) => {
                      this.setState({ Requirement: e.target.value });
                    }}
                  />
                </label>
              </div>

              <div className="col-md-12">
                <button
                  type="submit"
                  value="Subscribe"
                  name="subscribe"
                  id="mc-embedded-subscribe"
                  className="button"
                >
                  Submit
                </button>
              </div>
            </form>
          </div>
        </section>
      );
    }
  }
}

export default GetInTouch;
