import "./Header.css";
import React from "react";
import Phone from "../assets/phone.png";
import Email from "../assets/email.png";
import Home from "../assets/house.png";
import About from "../assets/house-design.png";
import Plans from "../assets/plans-design.png";
import Elevations from "../assets/buildings.png";
import Interiors from "../assets/interior-design.png";
import { Link } from "react-router-dom";


class Header extends React.Component {
  constructor(props) {
    super(props);
    this.state = {addClass: false}

    this.state = {
      items: [],
      isLoaded: false,
    };
  }

  toggle() {
    this.setState({addClass: !this.state.addClass});
  }

  componentDidMount() {
    fetch(
      "https://dreamhousemakerz.com/production/index.php/wp-json/wp/v2/header_footer/103/"
    )
      .then((result) => result.json())
      .then((json) => {
        this.setState({
          items: json,
          isLoaded: true,
        });
      });
  }

  render() {

    let boxClass = ["header-bottom-right"];
    if(this.state.addClass) {
      boxClass.push('header-block');
    }

    let toggleClass = ["toggle-bt-mb"];
    if(this.state.addClass) {
      toggleClass.push('toggle-close');
    }

    var { isLoaded, items } = this.state;

    if (!isLoaded) {
      return <div className="loader"></div>;
    } else {
      return (
        <header>
          <div className="header-top-section">
            <div className="container-size">
              <div className="header-top-inner">
                <div className="header-top-left">
                  <p>{items.acf.header_top_section.left_title}</p>
                </div>
                <div className="header-top-right">
                  <ul>
                    <li>
                      <a href={`tel:${items.acf.header_top_section.phone}`}>
                        <img alt="Phone" src={Phone} />{" "}
                        <span>+{items.acf.header_top_section.phone}</span>
                      </a>
                    </li>
                    <li>
                      <a href={`mailto:${items.acf.header_top_section.email}`}>
                        <img alt="Email" src={Email} />
                        <span>{items.acf.header_top_section.email}</span>
                      </a>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
          <div className="header-bottom-section">
            <div className="container-size">
              <div className="header-bottom-inner">
                <div className="header-bottom-left">
                  <div className="site-logo">
                    <Link to="/">
                      <img
                        src={items.acf.header_bottom_section.logo}
                        alt="dreamhousemakerz"
                      />
                    </Link>
                    {/* className="toggle-bt-mb" */}

                    <div onClick={this.toggle.bind(this)} className={toggleClass.join(' ')}>
                          <span></span>
                          <span></span>
                          <span></span>
                    </div>
                  </div>
                </div>
                <div className={boxClass.join(' ')}>
                  <nav>
                    <ul>
                      <li>
                        <Link to="/">
                          <img alt="home" src={Home} /> <span>Home</span>
                        </Link>
                      </li>
                      <li>
                        <Link to="/about-us">
                          <img alt="about" src={About} /> <span>About Us</span>
                        </Link>
                      </li>
                      <li>
                        <Link to="plans">
                          <img alt="plans" src={Plans} /> <span>Plans</span>
                        </Link>
                      </li>
                      <li>
                        <Link to="/elevation">
                          <img alt="Elevations" src={Elevations} />{" "}
                          <span>Elevations</span>
                        </Link>
                      </li>
                      <li>
                        <Link to="/interiors">
                          <img alt="Interiors" src={Interiors} />{" "}
                          <span>Interiors</span>
                        </Link>
                      </li>
                    </ul>
                  </nav>
                </div>
              </div>
            </div>
          </div>
        </header>
      );
    }
  }
}

export default Header;
