import "./HomeBanner.css";
import React from "react";
import axios from "axios";
import { Link } from "react-router-dom";
import bgone from "./images/bg_one.png";
import bgtwo from "./images/bg_two.png";
import bgthree from "./images/bg_three.png";
import bgfour from "./images/bg_four.png";
import bgfive from "./images/bg_five.png";

import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "swiper/css/navigation";
import { Autoplay, Navigation } from "swiper";


class HomeBanner extends React.Component {
  state = {
    persons: [],
  };
  componentDidMount() {
    axios
      .get(
        `https://dreamhousemakerz.com/production/index.php/wp-json/wp/v2/pages/10`
      )
      .then((res) => {
        const persons = [res.data];
        this.setState({ persons });
      });
  }

  render() {
    return (
      <>
        <section className="home-banner-main" style={{ display : "none" }}> 
          <div className="home-banner-inner">
            {this.state.persons.map((person) => (
              <div key={person.id}>
                <p>{person.acf.banner_section.top_title}</p>
                <h1>{person.acf.banner_section.banner_title}</h1>
                <h2>{person.acf.banner_section.sub_title}</h2>
                <Link to="/contact">
                  {person.acf.banner_section.button_title}
                </Link>
              </div>
            ))}
          </div>
        </section>

        <section>
          <div className="container-full">
            <div className="swiper-wrap">
              <Swiper

                autoplay={{
                  delay: 2000,
                  disableOnInteraction: false,
                }}

                navigation={true}
                modules={[Autoplay, Navigation]}
                loop={true}
                slidesPerView={1}
                spaceBetween={10}
                pagination={{
                  clickable: true,
                }}
                breakpoints={{
                  640: {
                    slidesPerView: 1,
                    spaceBetween: 20,
                  },
                  768: {
                    slidesPerView: 1,
                    spaceBetween: 40,
                  },
                  1024: {
                    slidesPerView: 1,
                    spaceBetween: 50,
                  },
                }}
                className="mySwiper"
                
              >
                <SwiperSlide>
                  <div className="banner-full">
                    <div className="container-heading">
                      <img alt="elevation leve drawing" src={bgfive} />
                      <div className="heading-box">
                        {this.state.persons.map((person) => (
                          <div key={person.id}>
                            <span>{person.acf.banner_section.top_title}</span>
                            <h1>{person.acf.banner_section.banner_title}</h1>
                            <p>{person.acf.banner_section.sub_title}</p>
                            <Link to="/contact">
                              {person.acf.banner_section.button_title}
                            </Link>
                          </div>
                        ))}
                      </div>
                    </div>
                  </div>
                </SwiperSlide>
                <SwiperSlide>
                  <div className="banner-full">
                    <div className="container-heading">
                      <img alt="elevation leve drawing" src={bgone} />
                      <div className="heading-box">
                        {this.state.persons.map((person) => (
                          <div key={person.id}>
                            <span>{person.acf.banner_section.top_title}</span>
                            <h1>{person.acf.banner_section.banner_title}</h1>
                            <p>{person.acf.banner_section.sub_title}</p>
                            <Link to="/contact">
                              {person.acf.banner_section.button_title}
                            </Link>
                          </div>
                        ))}
                      </div>
                    </div>
                  </div>
                </SwiperSlide>
                <SwiperSlide>
                  <div className="banner-full">
                    <div className="container-heading">
                      <img alt="elevation leve drawing" src={bgtwo} />
                      <div className="heading-box">
                        {this.state.persons.map((person) => (
                          <div key={person.id}>
                            <span>{person.acf.banner_section.top_title}</span>
                            <h1>{person.acf.banner_section.banner_title}</h1>
                            <p>{person.acf.banner_section.sub_title}</p>
                            <Link to="/contact">
                              {person.acf.banner_section.button_title}
                            </Link>
                          </div>
                        ))}
                      </div>
                    </div>
                  </div>
                </SwiperSlide>
                <SwiperSlide>
                  <div className="banner-full">
                    <div className="container-heading">
                      <img alt="elevation leve drawing" src={bgthree} />
                      <div className="heading-box">
                        {this.state.persons.map((person) => (
                          <div key={person.id}>
                            <span>{person.acf.banner_section.top_title}</span>
                            <h1>{person.acf.banner_section.banner_title}</h1>
                            <p>{person.acf.banner_section.sub_title}</p>
                            <Link to="/contact">
                              {person.acf.banner_section.button_title}
                            </Link>
                          </div>
                        ))}
                      </div>
                    </div>
                  </div>
                </SwiperSlide>
                <SwiperSlide>
                  <div className="banner-full">
                    <div className="container-heading">
                      <img alt="elevation leve drawing" src={bgfour} />
                      <div className="heading-box">
                        {this.state.persons.map((person) => (
                          <div key={person.id}>
                            <span>{person.acf.banner_section.top_title}</span>
                            <h1>{person.acf.banner_section.banner_title}</h1>
                            <p>{person.acf.banner_section.sub_title}</p>
                            <Link to="/contact">
                              {person.acf.banner_section.button_title}
                            </Link>
                          </div>
                        ))}
                      </div>
                    </div>
                  </div>
                </SwiperSlide>
           
              </Swiper>
            </div>
          </div>
        </section>
      </>
    );
  }
}

export default HomeBanner;
