import React, { useState, useEffect } from 'react';
import './Blog.css';
import { Link } from 'react-router-dom';


function Blog() {

    const [Users, fetchUsers] = useState([]);
    const [isLoading, setIsLoading] = useState(false);

    useEffect(() => {
        const getData = () => {
            setIsLoading(true);
            fetch(`https://dreamhousemakerz.com/production/index.php/wp-json/wp/v2/posts`)
                .then((res) => res.json())
                .then((res) => {
                    //console.log(res)
                    fetchUsers(res)
                    setIsLoading(false);
                })
        }
        getData();
    }, []);

    return (
        <>
            <section className='blog-banner-main'>
                <div className='blog-banner-sec'>
                    <img alt="blog" src="https://dreamhousemakerz.com/production/wp-content/uploads/2022/12/inner-pages-banner.jpg" />
                </div>
                <div className="site-title-style">
                    <h1>Blog</h1>
                </div>
            </section>
            {isLoading ? <div className="loader-spinner-p"> <div className="lds-spinner"><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div></div></div> :
                <section className='blog-list-main'>
                    <div className='container-size'>
                        <div className='blog-list-inner'>
                            {Users.map((item, i) => {
                                return <>
                                    <div className='blog-list' key={i}>
                                        <div className='blog-image'>
                                            <Link to={`/blog/${item.slug}`}><img src={item.featured_url} /></Link>
                                        </div>
                                        <div className='blog-content'>
                                            <h3>{item.title.rendered}</h3>
                                            <div className='blog-excerpt' dangerouslySetInnerHTML={{ __html: item.excerpt.rendered.substring(0, 100) }} />
                                            <Link to={`/blog/${item.slug}`}>Read More</Link>
                                        </div>
                                    </div>
                                </>
                            })}
                        </div>
                    </div>
                </section>
            }
        </>
    );
}
export default Blog;