import React from 'react';
import './FaqBanner.css';
import BannerImg from "./images/about-banner.jpg";


class FaqBanner extends React.Component {
    render() {
        return (
            <section className='terms-banner-main'>
                <div className='terms-banner-sec'>
                    <img alt="Terms" src={BannerImg} />
                </div>
            </section>
        );
    }
}
export default FaqBanner;

