import './Categories.css';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/navigation';
import { Autoplay, Navigation } from "swiper";
import React from 'react';
import { Link } from 'react-router-dom';



class Categories extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            items: [],
            isLoaded: false
        }
    }

    componentDidMount() {
        fetch('https://dreamhousemakerz.com/production/index.php/wp-json/wp/v2/service_category/?parent=0')
            .then(result => result.json())
            .then(json => {
                this.setState({
                    items: json,
                    isLoaded: true
                })
            });

    }

    render() {
        var { isLoaded, items } = this.state;
        if (!isLoaded) {
            return (
                <div className='loader'></div>
            );
        } else {
            return (
                <section className='categories-main'>
                    <div className='site-title-style'>
                        <h3 className='categories-title-p'>Categories</h3>
                    </div>
                    <div className='categories-slider'>
                        <Swiper
                         
                            autoplay={{
                                delay: 2000,
                                disableOnInteraction: false,
                              }}
              
                              navigation={true}
                              modules={[Autoplay, Navigation]}

                            loop={true}
                            slidesPerView={4}
                            spaceBetween={40}
                            pagination={{
                                clickable: true,
                            }}
                            breakpoints={{
                                "@0.00": {
                                    slidesPerView: 1,
                                    spaceBetween: 30,
                                },
                                "@0.75": {
                                    slidesPerView: 1,
                                    spaceBetween: 30,
                                },
                                "@1.00": {
                                    slidesPerView: 2,
                                    spaceBetween: 30,
                                },
                                "@1.50": {
                                    slidesPerView: 4,
                                    spaceBetween: 30,
                                },
                            }}

                            className="categoriesSwiper">
                            {items.map((data, i) => (
                                <SwiperSlide key={i}>
                                    <div className='categories-inner'>
                                        <Link to={data.acf.custom_url}>
                                            <div className='categories-img' id={data.id} ><img alt={data.name} src={data.acf.category_image} /></div>
                                            <h3>{data.name}</h3>
                                        </Link>
                                    </div>
                                </SwiperSlide>
                            ))}
                        </Swiper>
                    </div>
                </section>
            );
        }
    }


}


export default Categories;