import './ProjectCompleted.css';
import React from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/navigation';
import { Autoplay, Navigation } from "swiper";


class ProjectCompleted extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            items: [],
            isLoaded: false
        }
    }

    componentDidMount() {
        fetch('https://dreamhousemakerz.com/production/index.php/wp-json/wp/v2/project_completed?per_page=40&_embed')
            .then(result => result.json())
            .then(json => {
                this.setState({
                    items: json,
                    isLoaded: true
                })
            });
    }

    render() {

        var { isLoaded, items } = this.state;

        if (!isLoaded) {
            return (
                <div className='loader'></div>
            );
        } else {
            return (
                <section className='project-completed-main'>
                    <div className='site-title-style'>
                        <h3>Project Completed in PAN India</h3>
                    </div>
                    <div className='project-completed-size'>
                        <Swiper
                              autoplay={{
                                delay: 2000,
                                disableOnInteraction: false,
                              }}
                              navigation={true}
                              modules={[Autoplay, Navigation]}

                            loop={true}
                            slidesPerView={6}
                            spaceBetween={40}
                            
                            breakpoints={{
                                "@0.00": {
                                  slidesPerView: 1.3,
                                  spaceBetween: 15,
                                },
                                "@0.75": {
                                  slidesPerView: 1.3,
                                  spaceBetween: 15,
                                },
                                "@1.00": {
                                  slidesPerView: 3,
                                  spaceBetween: 15,
                                },
                                "@1.50": {
                                  slidesPerView: 6,
                                  spaceBetween: 30,
                                },
                              }}

                            className="completedSwiper">
                            {items.map((data, i) => (
                                <SwiperSlide key={i}>
                                    <div className='completed-inner'>
                                        <div className='completed-img'><img alt={data.title.rendered} src={data._embedded['wp:featuredmedia']['0'].source_url} /></div>
                                        <h3>{data.title.rendered}</h3>
                                        <p>{data.acf.total_projects_number} Projects</p>
                                    </div>
                                </SwiperSlide>
                            ))}
                        </Swiper>
                    </div>
                </section>
            );
        }
    }
}


export default ProjectCompleted;