import React, { useState, useEffect } from 'react';
import './Interiors.css'
import BannerImg from "./images/banner-interior.jpg";
import { Link } from "react-router-dom";
import square from "./images/square-feet.png";
import east from "./images/east-direction.png";
import squirearea from "./images/squire-area.png";
import {Helmet} from "react-helmet";


export default function Interiors() {

    if (localStorage.getItem("Interiors-Bathroom") == null) {
        var user1 = [];
        localStorage.setItem('Interiors-Bathroom', JSON.stringify(user1));
    }

    if (localStorage.getItem("Interiors-Bedroom") == null) {
        var user2 = [];
        localStorage.setItem('Interiors-Bedroom', JSON.stringify(user2));
    }

    if (localStorage.getItem("Interiors-Dining") == null) {
        var user3 = [];
        localStorage.setItem('Interiors-Dining', JSON.stringify(user3));
    }

    if (localStorage.getItem("Interiors-Kitchen") == null) {
        var user4 = [];
        localStorage.setItem('Interiors-Kitchen', JSON.stringify(user4));
    }

    if (localStorage.getItem("Interiors-LivingRoom") == null) {
        var user5 = [];
        localStorage.setItem('Interiors-LivingRoom', JSON.stringify(user5));
    }

    if (localStorage.getItem("Interiors-ShopOffice") == null) {
        var user6 = [];
        localStorage.setItem('Interiors-ShopOffice', JSON.stringify(user6));
    }

    const [BathroomData, BathroomDetails] = useState(JSON.parse(localStorage.getItem('Interiors-Bathroom')));
    const [BedroomData, BedroomDetails] = useState(JSON.parse(localStorage.getItem('Interiors-Bedroom')));
    const [DiningData, DiningDetails] = useState(JSON.parse(localStorage.getItem('Interiors-Dining')));
    const [KitchenData, KitchenDetails] = useState(JSON.parse(localStorage.getItem('Interiors-Kitchen')));
    const [LivingRoomData, LivingRoomDetails] = useState(JSON.parse(localStorage.getItem('Interiors-LivingRoom')));
    const [ShopOfficeData, ShopOfficeDetails] = useState(JSON.parse(localStorage.getItem('Interiors-ShopOffice')));

    useEffect(() => {
        const getInteriorsData = () => {
            // Bathroom
            fetch(`https://dreamhousemakerz.com/production/index.php/wp-json/wp/v2/services?service_category=45&per_page=6`)
                .then(async (res) => res.json())
                .then((res) => {
                    localStorage.setItem('Interiors-Bathroom', JSON.stringify(res));
                    BathroomDetails(JSON.parse(localStorage.getItem('Interiors-Bathroom')));
                    
                });
               

            // Bedroom
            fetch(`https://dreamhousemakerz.com/production/index.php/wp-json/wp/v2/services?service_category=48&per_page=6`)
                .then(async (res) => res.json())
                .then((res) => {
                    localStorage.setItem('Interiors-Bedroom', JSON.stringify(res));
                    BedroomDetails(JSON.parse(localStorage.getItem('Interiors-Bedroom')));
                });

            // Dining
            fetch(`https://dreamhousemakerz.com/production/index.php/wp-json/wp/v2/services?service_category=46&per_page=6`)
                .then(async (res) => res.json())
                .then((res) => {
                    localStorage.setItem('Interiors-Dining', JSON.stringify(res));
                    DiningDetails(JSON.parse(localStorage.getItem('Interiors-Dining')));
                })

            // Kitchen
            fetch(`https://dreamhousemakerz.com/production/index.php/wp-json/wp/v2/services?service_category=47&per_page=6`)
                .then(async (res) => res.json())
                .then((res) => {
                    localStorage.setItem('Interiors-Kitchen', JSON.stringify(res));
                    KitchenDetails(JSON.parse(localStorage.getItem('Interiors-Kitchen')));
                })

            // Living Room
            fetch(`https://dreamhousemakerz.com/production/index.php/wp-json/wp/v2/services?service_category=49&per_page=6`)
                .then(async (res) => res.json())
                .then((res) => {
                    localStorage.setItem('Interiors-LivingRoom', JSON.stringify(res));
                    LivingRoomDetails(JSON.parse(localStorage.getItem('Interiors-LivingRoom')));
                })

            // Shop and Office
            fetch(`https://dreamhousemakerz.com/production/index.php/wp-json/wp/v2/services?service_category=65&per_page=6`)
                .then(async (res) => res.json())
                .then((res) => {
                    localStorage.setItem('Interiors-ShopOffice', JSON.stringify(res));
                    ShopOfficeDetails(JSON.parse(localStorage.getItem('Interiors-ShopOffice')));
                })
        }

        getInteriorsData();
    }, []);

    return (
        <>
            <Helmet>
                <title>Get Complete Interior Design For Home | Dream House Makerz</title>
                <meta
                name='description'
                content='We create interior design for home which is a blend of traditional and contemporary styles specific to your personal needs.' 
                />
            </Helmet>
            <section className='about-banner-main'>
                <div className='about-banner-sec '>
                    <img alt="Interiors" src={BannerImg} />
                </div>
            </section>

            <section className='interiors-category-sec'>
            
                <div className='row'>
                    <div className='list-category-box'>
                        <div className="cat-name">Bedrooms</div>
                        {BedroomData.map((item, i) => {
                            return <div className='cate-area-box' key={i}>
                                <div className='cate-area-left'>
                                    <Link to={`/interiors/${item.slug}`}><img alt={item.acf.code} src={item.featured_url} /></Link>
                                </div>
                                <div className='cate-list-right'>
                                    <div className='cate-inner-top'>
                                        <div className='code-area'>
                                            <p>DESIGN CODE</p>
                                            <h4>{item.acf.code}</h4>
                                        </div>
                                        <ul>
                                            <li><img alt='plot_size' src={squirearea} /><span>{item.acf.plot_size}</span></li>
                                            <li><img alt='area' src={square} /><span>{item.acf.area}</span></li>
                                            <li><img alt='plot_facing' src={east} /><span> {item.acf.plot_facing}</span></li>
                                        </ul>
                                        <h3>Simplex House Plan</h3>
                                    </div>
                                    <div className='cate-inner-bottome'>
                                        <Link className="btn" type="button" to={`/interiors/${item.slug}`}>View Details</Link>
                                    </div>
                                </div>
                            </div>
                        })}
                        <div className="btn-view-more">
                            <Link className="btn" type="button" to="/services/bedroom">View More</Link>
                        </div>
                    </div>
                </div>
            </section>

            <section className='interiors-category-sec interiors-category-bg'>
           
                <div className='row'>
                    <div className='list-category-box'>
                        <div className="cat-name">Dining</div>
                        {DiningData.map((item, i) => {
                            return <div className='cate-area-box' key={i}>
                                <div className='cate-area-left'>
                                    <Link to={`/interiors/${item.slug}`}><img alt={item.acf.code} src={item.featured_url} /></Link>
                                </div>
                                <div className='cate-list-right'>
                                    <div className='cate-inner-top'>
                                        <div className='code-area'>
                                            <p>DESIGN CODE</p>
                                            <h4>{item.acf.code}</h4>
                                        </div>
                                        <ul>
                                            <li><img alt='plot_size' src={squirearea} /><span>{item.acf.plot_size}</span></li>
                                            <li><img alt='area' src={square} /><span>{item.acf.area}</span></li>
                                            <li><img alt='plot_facing' src={east} /><span> {item.acf.plot_facing}</span></li>
                                        </ul>
                                        <h3>Simplex House Plan</h3>
                                    </div>
                                    <div className='cate-inner-bottome'>
                                        <Link className="btn" type="button" to={`/interiors/${item.slug}`}>View Details</Link>
                                    </div>
                                </div>
                            </div>
                        })}
                        <div className="btn-view-more">
                            <Link className="btn" type="button" to="/services/dining">View More</Link>
                        </div>
                    </div>
                </div>
            </section>

            
            <section className='interiors-category-sec'>
            
                <div className='row'>
                    <div className='list-category-box'>
                        <div className="cat-name">Kitchen</div>
                        {KitchenData.map((item, i) => {
                            return <div className='cate-area-box' key={i}>
                                <div className='cate-area-left'>
                                        
                                    <Link to={`/interiors/${item.slug}`}><img alt={item.acf.code} src={item.featured_url} /></Link>
                                </div>
                                <div className='cate-list-right'>
                                    <div className='cate-inner-top'>
                                        <div className='code-area'>
                                            <p>DESIGN CODE</p>
                                            <h4>{item.acf.code}</h4>
                                        </div>
                                        <ul>
                                            <li><img alt='plot_size' src={squirearea} /><span>{item.acf.plot_size}</span></li>
                                            <li><img alt='area' src={square} /><span>{item.acf.area}</span></li>
                                            <li><img alt='plot_facing' src={east} /><span> {item.acf.plot_facing}</span></li>
                                        </ul>
                                        <h3>Simplex House Plan</h3>
                                    </div>
                                    <div className='cate-inner-bottome'>
                                    
                                        <Link className="btn" type="button" to={`/interiors/${item.slug}`}>View Details</Link>
                                    </div>
                                </div>
                            </div>
                        })}
                        <div className="btn-view-more">
                            <Link className="btn" type="button" to="/services/kitchen">View More</Link>
                        </div>
                    </div>
                </div>
            </section>

            <section className='interiors-category-sec interiors-category-bg'>
                
                <div className='row'>
                    <div className='list-category-box'>
                        <div className="cat-name">Living Room</div>
                        {LivingRoomData.map((item, i) => {
                            return <div className='cate-area-box' key={i}>
                                <div className='cate-area-left'>
                                    <Link to={`/interiors/${item.slug}`}><img alt={item.acf.code} src={item.featured_url} /></Link>
                                </div>
                                <div className='cate-list-right'>
                                    <div className='cate-inner-top'>
                                        <div className='code-area'>
                                            <p>DESIGN CODE</p>
                                            <h4>{item.acf.code}</h4>
                                        </div>
                                        <ul>
                                            <li><img alt='plot_size' src={squirearea} /><span>{item.acf.plot_size}</span></li>
                                            <li><img alt='area' src={square} /><span>{item.acf.area}</span></li>
                                            <li><img alt='plot_facing' src={east} /><span> {item.acf.plot_facing}</span></li>
                                        </ul>
                                        <h3>Simplex House Plan</h3>
                                    </div>
                                    <div className='cate-inner-bottome'>
                                        <Link className="btn" type="button" to={`/interiors/${item.slug}`}>View Details</Link>
                                    </div>
                                </div>
                            </div>
                        })}
                        <div className="btn-view-more">
                            <Link className="btn" type="button" to="/services/living-room">View More</Link>
                        </div>
                    </div>
                </div>
            </section>

            <section className='interiors-category-sec'>
               
                <div className='row'>
                    <div className='list-category-box'>
                        <div className="cat-name">Shop and Office</div>
                        {ShopOfficeData.map((item, i) => {
                            return <div className='cate-area-box' key={i}>
                                <div className='cate-area-left'>
                                    <Link to={`/interiors/${item.slug}`}><img alt={item.acf.code} src={item.featured_url} /></Link>
                                </div>
                                <div className='cate-list-right'>
                                    <div className='cate-inner-top'>
                                        <div className='code-area'>
                                            <p>DESIGN CODE</p>
                                            <h4>{item.acf.code}</h4>
                                        </div>
                                        <ul>
                                            <li><img alt='plot_size' src={squirearea} /><span>{item.acf.plot_size}</span></li>
                                            <li><img alt='area' src={square} /><span>{item.acf.area}</span></li>
                                            <li><img alt='plot_facing' src={east} /><span> {item.acf.plot_facing}</span></li>
                                        </ul>
                                        <h3>Simplex House Plan</h3>
                                    </div>
                                    <div className='cate-inner-bottome'>
                                        <Link className="btn" type="button" to={`/interiors/${item.slug}`}>View Details</Link>
                                    </div>
                                </div>
                            </div>
                        })}
                        <div className="btn-view-more">
                            <Link className="btn" type="button" to="/services/shop-and-office">View More</Link>
                        </div>
                    </div>
                </div>
            </section>

            <section className='interiors-category-sec interiors-category-bg'>
                
                <div className='row'>
                    <div className='list-category-box'>
                        <div className="cat-name">Bathroom</div>
                        {BathroomData.map((item, i) => {
                            return <div className='cate-area-box' key={i}>
                                <div className='cate-area-left'>
                                    <Link to={`/interiors/${item.slug}`}><img alt={item.acf.code} src={item.featured_url} /></Link>
                                </div>
                                <div className='cate-list-right'>
                                    <div className='cate-inner-top'>
                                        <div className='code-area'>
                                            <p>DESIGN CODE</p>
                                            <h4>{item.acf.code}</h4>
                                        </div>
                                        <ul>
                                            <li><img alt='plot_size' src={squirearea} /><span>{item.acf.plot_size}</span></li>
                                            <li><img alt='area' src={square} /><span>{item.acf.area}</span></li>
                                            <li><img alt='plot_facing' src={east} /><span> {item.acf.plot_facing}</span></li>
                                        </ul>
                                        <h3>Simplex House Plan</h3>
                                    </div>
                                    <div className='cate-inner-bottome'>
                                        <Link className="btn" type="button" to={`/interiors/${item.slug}`}>View Details</Link>
                                    </div>
                                </div>
                            </div>
                        })}
                        <div className="btn-view-more">
                            <Link className="btn" type="button" to="/services/bathroom">View More</Link>
                        </div>
                    </div>
                </div>
            </section>
        </>
    );
}