import React, { useState, useEffect } from 'react';
import BannerImg from "./images/banner-farm-house.jpg";
import { Link } from "react-router-dom";
import square from "./images/square-feet.png";
import east from "./images/east-direction.png";
import squirearea from "./images/squire-area.png";
import {Helmet} from "react-helmet";

export default function Apartment() {

    if (localStorage.getItem("Apartment") == null) {
        var user1 = [];
        localStorage.setItem('Apartment', JSON.stringify(user1));
    }

    const [ApartmentData, ApartmentDetails] = useState(JSON.parse(localStorage.getItem('Apartment')));

    useEffect(() => {
        const getSimplexData = () => {
            fetch(`https://dreamhousemakerz.com/production/index.php/wp-json/wp/v2/services?service_category=63&per_page=100`)
                .then(async (res) => res.json())
                .then((res) => {
                    localStorage.setItem('Apartment', JSON.stringify(res));
                    ApartmentDetails(JSON.parse(localStorage.getItem('Apartment')));
                });
        }

        getSimplexData()
    }, [])

    return (
        <>
            <Helmet>
                <title>Best Residential Apartments Design Plan | Dream House Makerz</title>
                <meta
                name='description'
                content='We create the best residential apartments design that resembles your vision for a comfortable home. Map out your apartments design plan with us!' 
                />
            </Helmet>
            <section className='about-banner-main'>
                <div className='about-banner-sec'>
                    <img alt="elevation banner" src={BannerImg} />
                </div>
            </section>


            <section className='elevations-section'>
                <div className='container-size'>
                    <div className='heading-plan'>
                        <h1>Residential Apartment Design</h1>
                        <p>Apartment design needs to be spacious enough regardless of its dimensions. Therefore, 
                            our team of designers and architectures craft the designs that incorporate space management giving it a
                             liveable essence too. Aesthetically sound, the apartment design will be best for a good number of people 
                             to live happily.</p>
                    </div>

                    <div className='row'>
                        <div className='list-category-box'>
                            {ApartmentData.map((item, i) => {
                                return <div className='cate-area-box' key={i}>
                                    <div className='cate-area-left'>
                                        <Link to={`/apartment/${item.slug}`}><img alt={item.acf.code} src={item.featured_url} /></Link>
                                    </div>
                                    <div className='cate-list-right'>
                                        <div className='cate-inner-top'>
                                            <div className='code-area'>
                                                <p>DESIGN CODE</p>
                                                <h4>{item.acf.code}</h4>
                                            </div>
                                            <ul>
                                                <li><img alt='plot_size' src={squirearea} /><span>{item.acf.plot_size}</span></li>
                                                <li><img alt='area' src={square} /><span>{item.acf.area}</span></li>
                                                <li><img alt='plot_facing' src={east} /><span> {item.acf.plot_facing}</span></li>
                                            </ul>
                                            <h3>Simplex House Plan</h3>
                                        </div>
                                        <div className='cate-inner-bottome'>
                                            <Link className="btn" type="button" to={`/apartment/${item.slug}`}>View Details</Link>
                                        </div>
                                    </div>
                                </div>
                            })}
                        </div>
                    </div>
                </div>
            </section>
        </>

    )
}