import React from "react";
import "./Services.css";
import ServicesBanner from "./services-banner/ServicesBanner";
import Elevation from "./images/3d-elevation.jpg";
import Apartment from "./images/apartment.png";
import Bungalow from "./images/bungalow.png";
import FarmHouse from "./images/farm-house.png";
import Interiors from "./images/interiors.jpg";
import Plans from "./images/plans.jpg";
import ShopWithHouse from "./images/shop-with-house.png";
import { Link } from "react-router-dom";

export default function Services() {
    return (
        <>
            <ServicesBanner />

            <div className="services-list-sec">
                <h1 className="Services-title">Services</h1>
                <div className="container-size">
                    <div className="services-list-inner">
                        <div className="services-list-box">
                            <Link to="/elevation">
                                <div className="services-list-img"><img alt="3D Elevation" src={Elevation} /></div>
                                <h3>3D Elevation</h3>
                            </Link>
                        </div>

                        <div className="services-list-box">
                            <Link to="/apartment">
                                <div className="services-list-img"><img alt="Apartment" src={Apartment} /></div>
                                <h3>Apartment</h3>
                            </Link>
                        </div>
                        <div className="services-list-box">
                            <Link to="/bungalow">
                                <div className="services-list-img"><img alt="Bungalow" src={Bungalow} /></div>
                                <h3>Bungalow</h3>
                            </Link>
                        </div>
                        <div className="services-list-box">
                            <Link to="/farm-house">
                                <div className="services-list-img"><img alt="Farm House" src={FarmHouse} /></div>
                                <h3>Farm House</h3>
                            </Link>
                        </div>
                        <div className="services-list-box">
                            <Link to="/interiors">
                                <div className="services-list-img"><img alt="Interiors" src={Interiors} /></div>
                                <h3>Interiors</h3>
                            </Link>
                        </div>
                        <div className="services-list-box">
                            <Link to="/plans">
                                <div className="services-list-img"><img alt="Plans" src={Plans} /></div>
                                <h3>Plans</h3>
                            </Link>
                        </div>
                        <div className="services-list-box">
                            <Link to="/shop-with-house">
                                <div className="services-list-img"><img alt="Shop with house" src={ShopWithHouse} /></div>
                                <h3>Shop with house</h3>
                            </Link>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}