import React from "react";
import PlansBanner from "./plans-banner/PlansBanner";
import PlansSection from "./plan-section/PlansSection";
import {Helmet} from "react-helmet";


export default function Plans() {
    return (
          <>
          <Helmet>
        <title>Get Customized House Plan Design | House Floor Plan Design</title>
        <meta
        name='description'
        content='Customize your house plan design with a team of dedicated experts who specialize in handling the raw data and modifying it into exceptional house floor plan design.' 
        />
    </Helmet>
            <PlansBanner />
            <PlansSection />
          </>
    );
}