import React, { useState, useEffect } from 'react';
import './Elevation.css';
import BannerImg from "./images/elevation-banner.jpg";
import { Link } from "react-router-dom";
import square from "./images/square-feet.png";
import east from "./images/east-direction.png";
import squirearea from "./images/squire-area.png";
import {Helmet} from "react-helmet";

export default function Elevation() {

    if (localStorage.getItem("Elevation-Simplex") == null) {
        var user1 = [];
        localStorage.setItem('Elevation-Simplex', JSON.stringify(user1));
    }

    if (localStorage.getItem("Elevation-Duplex") == null) {
        var user2 = [];
        localStorage.setItem('Elevation-Duplex', JSON.stringify(user2));
    }

    if (localStorage.getItem("Elevation-Triplex") == null) {
        var user3 = [];
        localStorage.setItem('Elevation-Triplex', JSON.stringify(user3));
    }

    const [SimplexData, SimplexDetails] = useState(JSON.parse(localStorage.getItem('Elevation-Simplex')));
    const [DuplexData, DuplexDetails] = useState(JSON.parse(localStorage.getItem('Elevation-Duplex')));
    const [TriplexData, TriplexDetails] = useState(JSON.parse(localStorage.getItem('Elevation-Triplex')));

    const getSimplexData = () => {
        // Simplex
        fetch(`https://dreamhousemakerz.com/production/index.php/wp-json/wp/v2/services?service_category=53&per_page=6`)
            .then(async (res) => res.json())
            .then((res) => {
                localStorage.setItem('Elevation-Simplex', JSON.stringify(res));
                SimplexDetails(JSON.parse(localStorage.getItem('Elevation-Simplex')));
            });

        // Duplex
        fetch(`https://dreamhousemakerz.com/production/index.php/wp-json/wp/v2/services?service_category=52&per_page=6`)
            .then(async (res) => res.json())
            .then((res) => {
                localStorage.setItem('Elevation-Duplex', JSON.stringify(res));
                DuplexDetails(JSON.parse(localStorage.getItem('Elevation-Duplex')));
            });

        // Triplex
        fetch(`https://dreamhousemakerz.com/production/index.php/wp-json/wp/v2/services?service_category=51&per_page=6`)
            .then(async (res) => res.json())
            .then((res) => {
                localStorage.setItem('Elevation-Triplex', JSON.stringify(res));
                TriplexDetails(JSON.parse(localStorage.getItem('Elevation-Triplex')));
            })
    }

    useEffect(() => {
        getSimplexData()
    }, [])


    return (
        <>
        <Helmet>
            <title>Online House Elevation Design Home Front Elevation Design</title>
            <meta
            name='description'
            content='Make your dream home come to reality with our house elevation design; envision your home with a 3D Elevation
            model and create a home front elevation design that you like.' 
            />
        </Helmet>
            <section className='about-banner-main'>
                <div className='about-banner-sec'>
                    <img alt="elevation banner" src={BannerImg} />
                </div>
            </section>

            <section className='elevations-section demoo'>
                <div className='container-size'>
                    <div className='heading-plan'>
                        <h1>ELEVATIONS</h1>
                        {/* <p>Who won’t love to look and feel at their ‘Dream House’ model?</p>
                        <p>With our 3D Elevation model, you can actually visualize how amazing your space is going to look. Just after the completion of your floor plan, we will prepare your 3D view to your imagination that will help you to feel and live your dream home before it gets ready. It will give you a realistic view of your imagination. With the help of our 3D elevations, you can connect with your place even more. So, Let your home exterior be the topic of talks amongst your near ones.</p> */}
                      <p>Elevations Who won’t love to look and feel at their ‘Dream House’ model?<br/>
                       With our 3D Elevation model, you can actually visualize how amazing your space is going to look</p>
                    
                    </div>

                    <div className='row'>
                        <div className='list-category-box'>
                            <div className="cat-name">Simplex</div>
                            {SimplexData.map((item, i) => {
                                return <div className='cate-area-box' key={i}>
                                    <div className='cate-area-left'>
                                        <Link to={`/elevation/${item.slug}`}><img alt={item.acf.code} src={item.featured_url} /></Link>
                                    </div>
                                    <div className='cate-list-right'>
                                        <div className='cate-inner-top'>
                                            <div className='code-area'>
                                                <p>DESIGN CODE</p>
                                                <h4>{item.acf.code}</h4>
                                            </div>
                                            <ul>
                                                <li><img alt='plot_size' src={squirearea} /><span>{item.acf.plot_size}</span></li>
                                                <li><img alt='area' src={square} /><span>{item.acf.area}</span></li>
                                                <li><img alt='plot_facing' src={east} /><span> {item.acf.plot_facing}</span></li>
                                            </ul>
                                            <h3>Simplex House Plan</h3>
                                        </div>
                                        <div className='cate-inner-bottome'>
                                            <Link className="btn" type="button" to={`/elevation/${item.slug}`}>View Details</Link>
                                        </div>
                                    </div>
                                </div>
                            })}
                            <div className="btn-view-more">
                                <Link className="btn" type="button" to="/services/simplex">View More</Link>
                            </div>
                        </div>
                    </div>

                    <div className='row'>
                        <div className='list-category-box'>
                            <div className="cat-name">Duplex</div>
                            {DuplexData.map((item, i) => {
                                return <div className='cate-area-box' key={i}>
                                    <div className='cate-area-left'>
                                        <Link to={`/elevation/${item.slug}`}><img alt={item.acf.code} src={item.featured_url} /></Link>
                                    </div>
                                    <div className='cate-list-right'>
                                        <div className='cate-inner-top'>
                                            <div className='code-area'>
                                                <p>DESIGN CODE</p>
                                                <h4>{item.acf.code}</h4>
                                            </div>
                                            <ul>
                                                <li><img alt='plot_size' src={squirearea} /><span>{item.acf.plot_size}</span></li>
                                                <li><img alt='area' src={square} /><span>{item.acf.area}</span></li>
                                                <li><img alt='plot_facing' src={east} /><span> {item.acf.plot_facing}</span></li>
                                            </ul>
                                            <h3>Simplex House Plan</h3>
                                        </div>
                                        <div className='cate-inner-bottome'>
                                            <Link className="btn" type="button" to={`/elevation/${item.slug}`}>View Details</Link>
                                        </div>
                                    </div>
                                </div>
                            })}
                            <div className="btn-view-more">
                                <Link className="btn" type="button" to="/services/duplex">View More</Link>
                            </div>
                        </div>
                    </div>

                    <div className='row'>
                        <div className='list-category-box'>
                            <div className="cat-name">Triplex</div>
                            {TriplexData.map((item, i) => {
                                return <div className='cate-area-box' key={i}>
                                    <div className='cate-area-left'>
                                        <Link to={`/elevation/${item.slug}`}><img alt={item.acf.code} src={item.featured_url} /></Link>
                                    </div>
                                    <div className='cate-list-right'>
                                        <div className='cate-inner-top'>
                                            <div className='code-area'>
                                                <p>DESIGN CODE</p>
                                                <h4>{item.acf.code}</h4>
                                            </div>
                                            <ul>
                                                <li><img alt='plot_size' src={squirearea} /><span>{item.acf.plot_size}</span></li>
                                                <li><img alt='area' src={square} /><span>{item.acf.area}</span></li>
                                                <li><img alt='plot_facing' src={east} /><span> {item.acf.plot_facing}</span></li>
                                            </ul>
                                            <h3>Simplex House Plan</h3>
                                        </div>
                                        <div className='cate-inner-bottome'>
                                            <Link className="btn" type="button" to={`/elevation/${item.slug}`}>View Details</Link>
                                        </div>
                                    </div>
                                </div>
                            })}
                            <div className="btn-view-more">
                                <Link className="btn" type="button" to="/services/triplex">View More</Link>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </>
    );
}