import React, { useState, useEffect } from 'react';
import './ElevationsSingle.css';
import square from "./images/square-feet.png";
import east from "./images/east-direction.png";
import squirearea from "./images/squire-area.png";
import { useParams } from 'react-router-dom';
import "@fancyapps/ui/dist/fancybox.css";
import Fancybox from '../fancybox';
import {Helmet} from "react-helmet";

export default function ElevationsSingle() {

    const params = useParams();
    const { slug } = params;

    const [Users, fetchUsers] = useState([]);
    const [isLoading, setIsLoading] = useState(false);

    useEffect(() => {
        const getData = () => {
            setIsLoading(true);
            fetch(`https://dreamhousemakerz.com/production/index.php/wp-json/wp/v2/services?slug=${slug}`)
                .then((res) => res.json())
                .then((res) => {
                    fetchUsers(res);
                    setIsLoading(false);
                })
        }
        getData();
    }, [])

    return (

        <>
        {Users.map((item, i) => { return <>
            <Helmet>
                <title>{item.title.rendered}</title>
                <meta
                name='description'
                content='We create interior design for home which is a blend of traditional and contemporary styles specific to your personal needs.' 
                />
            </Helmet>
                  </>
                })}

            {isLoading ? <div className="loader-spinner-p"><div className="lds-spinner"><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div></div></div> :
                <>
                    <section className='plan-section plan-signle-page'>
                        <div className='container-size'>
                            <div className='drawing-inner'>
                                <div className='heading-plan'>
                                    {Users.map((item, i) => {
                                        return <h1 key={i}>{item.title.rendered}</h1>
                                    })}
                                </div>
                            </div>
                            <div className='row plan-signle-inner'>
                                <div className='model-area-sn'>
                                    <div className='popup-area'>
                                        {Users.map((item, i) => {
                                            return <>
                                                <Fancybox>
                                                    <a data-fancybox="gallery" href={item.featured_url} >
                                                        <img alt="Elevations thum" src={item.featured_url} />
                                                    </a>
                                                    <div className='planthum'>
                                                        {item.acf.image_1 !== false && item.acf.image_1 ?
                                                            <a data-fancybox="gallery" href={item.acf.image_1} >
                                                                <img alt="Elevations thum" src={item.acf.image_1} />
                                                            </a>
                                                            : ''}
                                                        {item.acf.image_2 !== false && item.acf.image_2 ?
                                                            <a data-fancybox="gallery" href={item.acf.image_2} >
                                                                <img alt="Elevations thum" src={item.acf.image_2} />
                                                            </a>
                                                            : ''}
                                                        {item.acf.image_3 !== false && item.acf.image_3 ?
                                                            <a data-fancybox="gallery" href={item.acf.image_3} >
                                                                <img alt="Elevations thum" src={item.acf.image_3} />
                                                            </a>
                                                            : ''}
                                                        {item.acf.image_4 !== false && item.acf.image_4 ?
                                                            <a data-fancybox="gallery" href={item.acf.image_4} >
                                                                <img alt="Elevations thum" src={item.acf.image_4} />
                                                            </a>
                                                            : ''}
                                                        {item.acf.image_5 !== false && item.acf.image_5 ?
                                                            <a data-fancybox="gallery" href={item.acf.image_5} >
                                                                <img alt="Elevations thum" src={item.acf.image_5} />
                                                            </a>
                                                            : ''}
                                                    </div>
                                                </Fancybox>
                                            </>
                                        })}
                                    </div>
                                </div>
                                <div className='details-sn'>
                                    <h2>DETAILS</h2>
                                    <div className='cate-inner-top'>
                                        <ul>
                                            {Users.map((item, i) => {
                                                return <>
                                                    {item.acf.plot_size !== '' ?
                                                        <li key={i}><img alt="plot_size" src={square} /><span> {item.acf.plot_size}</span></li>
                                                        : ''}
                                                    {item.acf.area !== '' ?
                                                        <li key={i}><img alt="area" src={east} /><span> {item.acf.area}</span></li>
                                                        : ''}
                                                    {item.acf.number_of_floors !== '' ?
                                                        <li key={i}><img alt="number_of_floors" src={squirearea} /><span> {item.acf.number_of_floors}</span></li>
                                                        : ''}
                                                    {item.acf.plot_facing !== '' ?
                                                        <li key={i}><img alt="plot_facing" src={squirearea} /><span> {item.acf.plot_facing}</span></li>
                                                        : ''}
                                                    {item.acf.city !== '' ?
                                                        <li key={i}><img alt="city" src={squirearea} /><span> {item.acf.city} </span></li>
                                                        : ''}
                                                    {item.acf.state !== '' ?
                                                        <li key={i}><img alt="state" src={squirearea} /><span> {item.acf.state} </span></li>
                                                        : ''}
                                                    {item.acf.design_code !== '' ?
                                                        <li key={i}><img alt={item.acf.design_code} src={squirearea} /><span> {item.acf.design_code} </span></li>
                                                        : ''}
                                                </>
                                            })}
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>

                    <section className='sn-form-plans' id='sn-form-plans'>
                        <div className='container-size'>
                            <div className='row'>
                                <div className='form-plans'>
                                    <h3>POST YOUR QUERY</h3>
                                    <form>
                                        <div className='row'>
                                            <div className='col-md-6'>
                                                <label>* NAME</label>
                                                <input type="name" className="form-control" placeholder="Name"></input>
                                            </div>
                                            <div className='col-md-6'>
                                                <label>* MOBILE NUMBER</label>
                                                <input type="mobile" className="form-control" placeholder="Your Mobile"></input>
                                            </div>
                                        </div>

                                        <div className='row'>
                                            <div className='col-md-6'>
                                                <label>* EMAIL ADDRESS</label>
                                                <input type="email" className="form-control" placeholder="Email Address"></input>
                                            </div>
                                            <div className='col-md-6'>
                                                <label>CITY</label>
                                                <input type="City" className="form-control" placeholder="City"></input>
                                            </div>
                                        </div>

                                        <div className='row'>
                                            <div className='col-md-12'>
                                                <label>* REQUIREMENTS</label>
                                                <input type="Requirment" className="form-control" placeholder="Requirment"></input>
                                            </div>
                                        </div>

                                        <div className='row'>
                                            <div className='col-md-12'>
                                                <label>* PLOT SIZE </label>
                                                <textarea type="text" rows="4" className="form-control" placeholder="Plot Size"></textarea>
                                            </div>
                                        </div>
                                        <div className='row'>
                                            <div className='col-md-12'>
                                                <button type='button'>Search</button>
                                            </div>
                                        </div>
                                    </form>
                                </div>
                            </div>
                        </div>
                    </section>
                </>
            }
        </>
    );
}