import { useRef, useState } from "react";
import emailjs from '@emailjs/browser';

function GetInTouchFrom() {
  const [isError, setIsError] = useState(null);
  const form = useRef()
  const sendEmail = (e) => {
    e.preventDefault();

    emailjs.sendForm('service_tl5enq2', 'template_j4jr118', form.current, 'G4oExdSRfMfCFrRZ0')
      .then((result) => {
          console.log(result.text);
          setIsError(false);
      }, (error) => {
          console.log(error.text);
          
      })
      .catch(() => setIsError(true)); 
      e.target.reset()

 
  };


  return (
    <>
      <section className="get-in-touch-main">
        <div className="get-in-touch-img">
          <img alt="Get in touch" src="https://dreamhousemakerz.com/production/wp-content/uploads/2022/07/get-in-touch-img.jpg" />
        </div>
        <div className="get-in-touch-form">
          <h3>Get in touch</h3>
          <p>Whether you're ready to sell residential, luxury, or land-we're ready to help get it done.</p>
          <form ref={form} onSubmit={sendEmail}>
              <div className="col-md-6">
                <label>
                  <input
                    type="text"
                    name="user_name"
                    placeholder="First name"
                    required />
                </label>
              </div>

              <div className="col-md-6">
                <label>
                  <input 
                    type="text"
                    name="user_phone"
                    placeholder="Your Number"
                    required />
                </label>
              </div>

              <div className="col-md-12">
                <label>
                  <input
                    type="email"
                    name="user_email"
                    placeholder="Email"
                    autoCapitalize="off"
                    autoCorrect="off"
                    required />
                </label>
              </div>

              <div className="col-md-6">
                <label htmlFor="MERGE3">
                  <select name="user_state" required>
                    <option value="State">State</option>
                    <option value="Andhra Pradesh">Andhra Pradesh</option>
                    <option value="Arunachal Pradesh">Arunachal Pradesh</option>
                    <option value="Assam">Assam</option>
                    <option value="Bihar">Bihar</option>
                    <option value="Chhattisgarh">Chhattisgarh</option>
                    <option value="Goa">Goa</option>
                    <option value="Gujarat">Gujarat</option>
                    <option value="Haryana">Haryana</option>
                    <option value="Himachal Pradesh">Himachal Pradesh</option>
                    <option value="Jammu and Kashmir">Jammu and Kashmir</option>
                    <option value="Jharkhand">Jharkhand</option>
                    <option value="Karnataka">Karnataka</option>
                    <option value="Kerala">Kerala</option>
                    <option value="Madhya Pradesh">Madhya Pradesh</option>
                    <option value="Maharashtra">Maharashtra</option>
                    <option value="Manipur">Manipur</option>
                    <option value="Meghalaya">Meghalaya</option>
                    <option value="Mizoram">Mizoram</option>
                    <option value="Nagaland">Nagaland</option>
                    <option value="Orissa">Orissa</option>
                    <option value="Punjab">Punjab</option>
                    <option value="Rajasthan">Rajasthan</option>
                    <option value="Sikkim">Sikkim</option>
                    <option value="Tamil Nadu">Tamil Nadu</option>
                    <option value="Tripura">Tripura</option>
                    <option value="Uttarakhand">Uttarakhand</option>
                    <option value="Uttar Pradesh">Uttar Pradesh</option>
                    <option value="West Bengal">West Bengal</option>
                    <option value="Tamil Nadu">Tamil Nadu</option>
                    <option value="Andaman and Nicobar Islands">
                      Andaman and Nicobar Islands
                    </option>
                    <option value="Chandigarh">Chandigarh</option>
                    <option value="Dadra and Nagar Haveli">
                      Dadra and Nagar Haveli
                    </option>
                    <option value="Daman and Diu">Daman and Diu</option>
                    <option value="Delhi">Delhi</option>
                    <option value="Lakshadweep">Lakshadweep</option>
                    <option value="Pondicherry">Pondicherry</option>
                  </select>
                </label>
              </div>

              <div className="col-md-6">
                <label>
                  <input
                    type="text"
                    name="user_city"
                    placeholder="City"
                    required />
                </label>
              </div>

              <div className="col-md-12">
                <label>
                  <input
                    type="text"
                    name="user_requirement"
                    placeholder="Requirement"
                    required />
                </label>
              </div>

              <div className="col-md-12">
                <button
                  type="submit"
                  value="Subscribe"
                  name="subscribe"
                  className="button">
                  Submit
                </button>
                { isError === true && (<div>Error!</div>) }
                { isError === false && (<div className="Success-msg">Thank you for subscribing!</div>) }
              </div>

            </form>
        </div>
      </section>
    </>
  );
}
export default GetInTouchFrom;
