import React, { useState, useEffect } from 'react';
import square from "./images/square-feet.png";
import east from "./images/east-direction.png";
import squirearea from "./images/squire-area.png";
import { useParams } from 'react-router-dom';
import "@fancyapps/ui/dist/fancybox.css";
import Fancybox from '../fancybox';
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "swiper/css/free-mode";
import "swiper/css/navigation";
import "swiper/css/thumbs";
import { FreeMode, Navigation, Thumbs } from "swiper";

export default function BungalowSingle() {
    
    const [thumbsSwiper, setThumbsSwiper] = useState(null);

    const params = useParams();
    const { slug } = params;
    console.warn(slug)

    const [Users, fetchUsers] = useState([]);
    const [isLoading, setIsLoading] = useState(false);
    useEffect(() => {
    const getData = () => {
        setIsLoading(true);
        fetch(`https://dreamhousemakerz.com/production/index.php/wp-json/wp/v2/services?slug=${slug}`)
            .then((res) => res.json())
            .then((res) => {
                localStorage.setItem('BungalowSingle', JSON.stringify(res));
                fetchUsers(JSON.parse(localStorage.getItem('BungalowSingle')));
                setIsLoading(false);
            })
    }
        getData();
    }, [])

    return (
        <>
        {isLoading ? <div className="loader-spinner-p"><div className="lds-spinner"><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div></div></div> :
            <>
                <section className='plan-section plan-signle-page'>
                    <div className='container-size'>
                        <div className='row plan-signle-inner'>
                            <div className='model-area-sn'>
                                <div className='popup-area'>
                                    {Users.map((item, i) => {
                                        return <>
                                            <Fancybox>
                                                <Swiper
                                                    style={{
                                                        "--swiper-navigation-color": "#000",
                                                        "--swiper-pagination-color": "#000",
                                                    }}
                                                    spaceBetween={10}
                                                    navigation={true}
                                                    thumbs={{ swiper: thumbsSwiper }}
                                                    modules={[FreeMode, Navigation, Thumbs]}
                                                    className="mySwiperint2"
                                                >

                                                    {item.featured_url !== false && item.featured_url ?
                                                        <SwiperSlide>
                                                            <a data-fancybox="gallery" href={item.featured_url} >
                                                                <img alt="Elevations thum" src={item.featured_url} />
                                                            </a>
                                                        </SwiperSlide> : ''}

                                                    {item.acf.image_1 !== false && item.acf.image_1 ?
                                                        <SwiperSlide>
                                                            <a data-fancybox="gallery" href={item.acf.image_1} >
                                                                <img alt="Elevations thum" src={item.acf.image_1} />
                                                            </a>
                                                        </SwiperSlide> : ''}

                                                    {item.acf.image_2 !== false && item.acf.image_2 ?
                                                        <SwiperSlide>
                                                            <a data-fancybox="gallery" href={item.acf.image_2} >
                                                                <img alt="Elevations thum" src={item.acf.image_2} />
                                                            </a>
                                                        </SwiperSlide> : ''}

                                                    {item.acf.image_3 !== false && item.acf.image_3 ?
                                                        <SwiperSlide>
                                                            <a data-fancybox="gallery" href={item.acf.image_3} >
                                                                <img alt="Elevations thum" src={item.acf.image_3} />
                                                            </a>
                                                        </SwiperSlide> : ''}

                                                    {item.acf.image_4 !== false && item.acf.image_4 ?
                                                        <SwiperSlide>
                                                            <a data-fancybox="gallery" href={item.acf.image_4} >
                                                                <img alt="Elevations thum" src={item.acf.image_4} />
                                                            </a>
                                                        </SwiperSlide> : ''}

                                                    {item.acf.image_5 !== false && item.acf.image_5 ?
                                                        <SwiperSlide>
                                                            <a data-fancybox="gallery" href={item.acf.image_5} >
                                                                <img alt="Elevations thum" src={item.acf.image_5} />
                                                            </a>
                                                        </SwiperSlide> : ''}

                                                    {item.acf.image_6 !== false && item.acf.image_6 ?
                                                        <SwiperSlide>
                                                            <a data-fancybox="gallery" href={item.acf.image_6} >
                                                                <img alt="Elevations thum" src={item.acf.image_6} />
                                                            </a>
                                                        </SwiperSlide> : ''}

                                                    {item.acf.image_7 !== false && item.acf.image_7 ?
                                                        <SwiperSlide>
                                                            <a data-fancybox="gallery" href={item.acf.image_7} >
                                                                <img alt="Elevations thum" src={item.acf.image_7} />
                                                            </a>
                                                        </SwiperSlide> : ''}

                                                    {item.acf.image_8 !== false && item.acf.image_8 ?
                                                        <SwiperSlide>
                                                            <a data-fancybox="gallery" href={item.acf.image_8} >
                                                                <img alt="Elevations thum" src={item.acf.image_8} />
                                                            </a>
                                                        </SwiperSlide> : ''}

                                                    {item.acf.image_9 !== false && item.acf.image_9 ?
                                                        <SwiperSlide>
                                                            <a data-fancybox="gallery" href={item.acf.image_9} >
                                                                <img alt="Elevations thum" src={item.acf.image_9} />
                                                            </a>
                                                        </SwiperSlide> : ''}

                                                    {item.acf.image_10 !== false && item.acf.image_10 ?
                                                        <SwiperSlide>
                                                            <a data-fancybox="gallery" href={item.acf.image_10} >
                                                                <img alt="Elevations thum" src={item.acf.image_10} />
                                                            </a>
                                                        </SwiperSlide> : ''}
                                                </Swiper>

                                                <Swiper
                                                    onSwiper={setThumbsSwiper}
                                                    spaceBetween={10}
                                                    slidesPerView={4}
                                                    navigation={true}
                                                    freeMode={true}
                                                    watchSlidesProgress={true}
                                                    modules={[FreeMode, Navigation, Thumbs]}
                                                    className="mySwiperint"
                                                >

                                                    {item.featured_url !== false && item.featured_url ?
                                                        <SwiperSlide>
                                                            <div className='planthum'>
                                                                <img alt="Elevations thum" src={item.featured_url} />
                                                            </div>
                                                        </SwiperSlide>
                                                        : ''}
                                                    {item.acf.image_1 !== false && item.acf.image_1 ?
                                                        <SwiperSlide>
                                                            <div className='planthum'>
                                                                <img alt="Elevations thum" src={item.acf.image_1} />
                                                            </div>
                                                        </SwiperSlide>
                                                        : ''}

                                                    {item.acf.image_2 !== false && item.acf.image_2 ?
                                                        <SwiperSlide>
                                                            <div className='planthum'>
                                                                <img alt="Elevations thum" src={item.acf.image_2} />
                                                            </div>
                                                        </SwiperSlide> : ''}

                                                    {item.acf.image_3 !== false && item.acf.image_3 ?
                                                        <SwiperSlide>
                                                            <div className='planthum'>
                                                                <img alt="Elevations thum" src={item.acf.image_3} />
                                                            </div>
                                                        </SwiperSlide> : ''}

                                                    {item.acf.image_4 !== false && item.acf.image_4 ?
                                                        <SwiperSlide>
                                                            <div className='planthum'>
                                                                <img alt="Elevations thum" src={item.acf.image_4} />
                                                            </div>
                                                        </SwiperSlide> : ''}

                                                    {item.acf.image_5 !== false && item.acf.image_5 ?
                                                        <SwiperSlide>
                                                            <div className='planthum'>
                                                                <img alt="Elevations thum" src={item.acf.image_5} />
                                                            </div>
                                                        </SwiperSlide> : ''}

                                                    {item.acf.image_6 !== false && item.acf.image_6 ?
                                                        <SwiperSlide>
                                                            <div className='planthum'>
                                                                <img alt="Elevations thum" src={item.acf.image_6} />
                                                            </div>
                                                        </SwiperSlide> : ''}

                                                    {item.acf.image_7 !== false && item.acf.image_7 ?
                                                        <SwiperSlide>
                                                            <div className='planthum'>
                                                                <img alt="Elevations thum" src={item.acf.image_7} />
                                                            </div>
                                                        </SwiperSlide> : ''}

                                                    {item.acf.image_8 !== false && item.acf.image_8 ?
                                                        <SwiperSlide>
                                                            <div className='planthum'>
                                                                <img alt="Elevations thum" src={item.acf.image_8} />
                                                            </div>
                                                        </SwiperSlide> : ''}

                                                    {item.acf.image_9 !== false && item.acf.image_9 ?
                                                        <SwiperSlide>
                                                            <div className='planthum'>
                                                                <img alt="Elevations thum" src={item.acf.image_9} />
                                                            </div>
                                                        </SwiperSlide> : ''}

                                                    {item.acf.image_10 !== false && item.acf.image_10 ?
                                                        <SwiperSlide>
                                                            <div className='planthum'>
                                                                <img alt="Elevations thum" src={item.acf.image_10} />
                                                            </div>
                                                        </SwiperSlide> : ''}
                                                </Swiper>
                                            </Fancybox>
                                        </>
                                    })}
                                </div>
                            </div>

                            <div className='details-sn'>
                                <div className='heading-plan'>
                                    {Users.map((item, i) => {
                                        return <>
                                            <h1 key={i}>{item.title.rendered}</h1>
                                            <div dangerouslySetInnerHTML={{ __html: item.acf.project_description }} />
                                        </>
                                    })}
                                </div>

                                <div className='cate-inner-top'>
                                    {Users.map((item, i) => {
                                        return <>
                                            <table className='table table-bordered'>
                                                <thead>
                                                    <tr>
                                                        <th>Project Description </th>
                                                        <th>Floor Description </th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    <tr>
                                                        <td>
                                                            {item.acf.plot_size !== '' ?
                                                                <p key={i}><img alt="plot_size" src={square} /><span> {item.acf.plot_size}</span></p>
                                                                : ''}
                                                        </td>
                                                        <td>750 sqft</td>
                                                    </tr>

                                                    <tr>
                                                        <td>
                                                            {item.acf.area !== '' ?
                                                                <p key={i}><img alt="area" src={east} /><span> {item.acf.area}</span></p>
                                                                : ''}
                                                        </td>
                                                        <td>Residential</td>
                                                    </tr>

                                                    <tr>
                                                        <td>
                                                            {item.acf.plot_facing !== '' ?
                                                                <p key={i}><img alt="plot_facing" src={squirearea} /><span> {item.acf.plot_facing} Facing</span></p>
                                                                : ''}
                                                        </td>
                                                        <td>South</td>
                                                    </tr>

                                                    <tr>
                                                        <td>{item.acf.name !== '' ?
                                                            <p key={i}><img alt="name" src={squirearea} /><span> {item.acf.name} </span></p>
                                                            : ''}</td>
                                                        <td>Santiniketan</td>
                                                    </tr>

                                                    <tr>
                                                        <td> {item.acf.location !== '' ?
                                                            <p key={i}><img alt="location" src={squirearea} /><span> {item.acf.location} </span></p>
                                                            : ''}</td>
                                                        <td>indore madhya pradesh</td>
                                                    </tr>

                                                    <tr>
                                                        <td>{item.acf.code !== '' ?
                                                            <p key={i}><img alt={item.acf.code} src={squirearea} /><span> {item.acf.code} </span></p>
                                                            : ''}</td>
                                                        <td>230</td>
                                                    </tr>

                                                </tbody>
                                            </table>
                                        </>
                                    })}
                                </div>
                            </div>
                        </div>
                    </div>
                </section>

                <section className='sn-form-plans' id='sn-form-plans'>
                    <div className='container-size'>
                        <div className='row'>
                            <div className='form-plans'>
                                <h3>POST YOUR QUERY</h3>
                                <form>
                                    <div className='row'>
                                        <div className='col-md-6'>
                                            <label>* NAME</label>
                                            <input type="name" className="form-control" placeholder="Name"></input>
                                        </div>
                                        <div className='col-md-6'>
                                            <label>* MOBILE NUMBER</label>
                                            <input type="mobile" className="form-control" placeholder="Your Mobile"></input>
                                        </div>
                                    </div>

                                    <div className='row'>
                                        <div className='col-md-6'>
                                            <label>* EMAIL ADDRESS</label>
                                            <input type="email" className="form-control" placeholder="Email Address"></input>
                                        </div>
                                        <div className='col-md-6'>
                                            <label>CITY</label>
                                            <input type="City" className="form-control" placeholder="City"></input>
                                        </div>
                                    </div>

                                    <div className='row'>
                                        <div className='col-md-12'>
                                            <label>* REQUIREMENTS</label>
                                            <input type="Requirment" className="form-control" placeholder="Requirment"></input>
                                        </div>
                                    </div>

                                    <div className='row'>
                                        <div className='col-md-12'>
                                            <label>* PLOT SIZE </label>
                                            <textarea type="text" rows="4" className="form-control" placeholder="Plot Size"></textarea>
                                        </div>
                                    </div>
                                    <div className='row'>
                                        <div className='col-md-12'>
                                            <button type='button'>Search</button>
                                        </div>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </section>
            </>
        }
    </>
    );
}