import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import square from "../plans/plans-banner/images/square-feet.png";
import east from "../plans/plans-banner/images/east-direction.png";
import squirearea from "../plans/plans-banner/images/squire-area.png";
import BannerImg from "./images/elevation-banner.jpg";

function Triplex() {

    if (localStorage.getItem("TriplexPage") == null) {
        var user = [];
        localStorage.setItem('TriplexPage', JSON.stringify(user));
    }
    const [TriplexData, TriplexDetails] = useState(JSON.parse(localStorage.getItem('TriplexPage')));

    useEffect(() => {

        const getTriplexData = () => {
            fetch(`https://dreamhousemakerz.com/production/index.php/wp-json/wp/v2/services?service_category=51`)
                .then(async (res) => res.json())
                .then((res) => {
                    localStorage.setItem('TriplexPage', JSON.stringify(res));
                    TriplexDetails(JSON.parse(localStorage.getItem('TriplexPage')));
                })
        }

        getTriplexData();

    }, []);


    return (
        <>
            <section className='about-banner-main'>
                <div className='about-banner-sec'>
                    <img alt="elevation banner" src={BannerImg} />
                </div>
            </section>
            <section className='elevations-section'>
                <div className='container-size'>
                    <div className='row'>
                        <div className='row'>
                            <div className="heading-plan">
                                <h1>Triplex</h1>
                                <p>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. </p>
                            </div>
                            <div className='list-category-box list-category-box-main'>
                                {TriplexData.map((item, i) => {
                                    return <div className='cate-area-box' key={i}>
                                        <div className='cate-area-left'>
                                            <Link to={`/elevation/${item.slug}`}><img alt={item.acf.code} src={item.featured_url} /></Link>
                                        </div>
                                        <div className='cate-list-right'>
                                            <div className='cate-inner-top'>
                                                <div className='code-area'>
                                                    <p>DESIGN CODE</p>
                                                    <h4>{item.acf.code}</h4>
                                                </div>
                                                <ul>
                                                    <li><img alt="plot_size" src={squirearea} /><span>{item.acf.plot_size}</span></li>
                                                    <li><img alt="area" src={square} /><span>{item.acf.area}</span></li>
                                                    <li><img alt="plot_facing" src={east} /><span> {item.acf.plot_facing}</span></li>
                                                </ul>
                                                <h3>Simplex House Plan</h3>
                                            </div>
                                            <div className='cate-inner-bottome'>
                                                <Link className="btn" type="button" to={`/elevation/${item.slug}`}>View Details</Link>
                                            </div>
                                        </div>
                                    </div>
                                })}
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </>
    );
}

export default Triplex;